html {
  min-height: 100vh;
}


@font-face {
  font-family: 'Trojan pro';
  font-style: normal;
  font-weight: 400;
  src: url('./assets/fonts/Trajan\ Pro\ Regular.ttf'); /* IE9 Compat Modes */
  src: local('Open Sans'), local('OpenSans'),
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('embedded-opentype'), /* IE6-IE8 */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('woff2'), /* Super Modern Browsers */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('woff'), /* Modern Browsers */
       url('./assets/fonts/Trajan\ Pro\ Regular.ttf') format('truetype'), /* Safari, Android, iOS */
}


body {
  margin: 0;
  padding: 0;
  font-family: 'Trojan pro';
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: 'trajan-pro-3', 'Trajan Pro', Georgia, 'Times New Roman';
}

*:focus {
  outline: none !important;
  -webkit-tap-highlight-color: transparent;
}

.App {
  text-align: center;
}

/* 
##Device = Desktops
##Screen = 1281px to higher resolution desktops
*/

@media (min-width: 1281px) {

  body {
      background: #282c34 url('./assets/img/background-landscape.jpg') center center no-repeat;
      background-size: cover;
  }

}

/* 
##Device = Laptops, Desktops
##Screen = B/w 1025px to 1280px
*/

@media (min-width: 1025px) and (max-width: 1280px) {

  body {
      background: url('./assets/img/background-landscape.jpg') center center no-repeat;
      background-size:cover;
  }

}

/* 
##Device = Tablets, Ipads (portrait)
##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) {

  body {
      background: url('./assets/img/background-portrait.jpg') center center no-repeat;
      background-size:cover;
  }


}

/* 
##Device = Tablets, Ipads (landscape)
##Screen = B/w 768px to 1024px
*/

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  body {
      background: url('./assets/img/background-landscape.jpg') center center no-repeat;
      background-size:cover;
  }

}

/* 
##Device = Low Resolution Tablets, Mobiles (Landscape)
##Screen = B/w 481px to 767px
*/

@media (min-width: 481px) and (max-width: 767px) {

  body {
      background: url('./assets/img/background-landscape.jpg') center center no-repeat;
      background-size:cover;
  }

}

/* 
##Device = Most of the Smartphones Mobiles (Portrait)
##Screen = B/w 320px to 479px
*/

@media (min-width: 320px) and (max-width: 480px) {

  body {
      background: url('./assets/img/background-portrait.jpg') center center no-repeat;
      background-size:cover;
  }

}